<h2>简介</h2>
<p><code>VUX</code>（读音 [v’ju:z]，同 <code>views</code>）是基于<code>WeUI</code>和<code>Vue</code>(2.x)开发的移动端UI组件库，主要服务于微信页面。</p>
<p>基于<code>webpack + vue-loader + vux</code>可以快速开发移动端页面，配合<code>vux-loader</code>方便你在<code>WeUI</code>的基础上定制需要的样式。</p>
<p><code>vux-loader</code>保证了组件按需使用，因此不用担心最终打包了整个vux的组件库代码。</p>
<p><code>VUX</code>并不完全依赖于<code>WeUI</code>，<code>VUX</code> 在 <code>WeUI</code> 的基础上扩展了多个常用组件，但是尽量保持整体UI样式接近<code>WeUI</code>的设计规范。</p>
<div class="warning">
<p>VUX 并不是一个能解决所有场景的完美解决方案(实际上也没有一个方案能解决所有问题)，也会出现某些<code>bug</code>或者某些特性不支持，所以如果遇到问题麻烦及时<strong>不带情绪正确反馈</strong>，<strong>我们乐于及时解决描述详细方便重现的问题</strong>。<br><br>
即使你不直接使用 <code>VUX</code> 组件代码, 你依然可以参考 VUX 代码来实现自己的组件库。如果一定程度上帮助到了你，那么维护这个项目也就有所意义。</p>
</div>
<h2>订阅版本发布通知</h2>
<p>请使用微信扫描</p>
<img src="https://ws1.sinaimg.cn/large/663d3650gy1fpno3msj6wj20dw0dw748.jpg" width="150">
<h2>提示</h2>
<div class="tip">
<p><code>VUX</code> 是<code>库</code>而非<code>框架</code>，虽然有专用的 <code>vux-loader</code>，但并不影响你自由地使用其他组件库或者工具库。<br><br></p>
<p><code>VUX</code> 使用的 CSS 预处理工具是 <code>less</code>(同 WeUI)，但(利益于 .vue 单文件组件的灵活性)这并不影响你使用 <code>SASS</code> 等其他预处理器。<br><br></p>
<p>用以表示该组件库时请使用大写名字 <code>VUX</code>，用在说明版本号时使用小写 <code>vux@2.x</code>。</p>
